<script>
import { dispose, init } from "klinecharts";
export default {
    name: "Futures",
    components: {},
    data() {
        return {
            loading: false,
            ws: null,
            kLineChart: null,
            klineData: [],
            symbol: "btcusdt",
            tick: {
                open: 0,
                close: 0,
                percent: 0,
            },

            radio: {
                temp: "",
                value: "1day",
            },
            slider: {
                marks: {
                    0: "0x",
                    25: "25x",
                    50: "50x",
                    75: "75x",
                    100: "100x",
                },
            },
            menu: {
                current: ["limit"],
            },
            list: [],
            orderbook_buy_list: [],
            orderbook_sell_list: [],
            market_trades_list: [],
        };
    },
    destroyed: function () {
        dispose("custom-theme-k-line");
        if (this.ws != null) {
            this.ws.close();
            this.ws = null;
        }
    },
    methods: {
        onClickOrderbook(item) {
            console.log("item", item);
        },
        onClickSymbol(item) {
            console.log("item", item.symbol, item);
            const symbol = item.symbol.toLocaleLowerCase();
            this.$router.push(`/futures?symbol=${symbol}usdt`);
            window.location.reload();
        },
        onChangeCallback(key) {
            console.log(key);
        },
        onChangeKline(e) {
            console.log(`checked = ${e.target.value}`);

            // { key: 'candle_solid', text: '蜡烛实心' },
            // { key: 'candle_stroke', text: '蜡烛空心' },
            // { key: 'candle_up_stroke', text: '蜡烛涨空心' },
            // { key: 'candle_down_stroke', text: '蜡烛跌空心' },
            // { key: 'ohlc', text: 'OHLC' },
            // { key: 'area', text: '面积图' }
            const period = e.target.value;

            if (period == "line") {
                this.kLineChart.setStyleOptions({
                    candle: { type: "area" },
                });
                return;
            } else {
                this.kLineChart.setStyleOptions({
                    candle: { type: "candle_solid" },
                });
            }

            this.generatedKLineDataList(this.symbol, period);
        },
        generatedKLineDataList(symbol, period, callback) {
            this.loading = true;
            const url =
                this.g.apiUrl + `/api/kline?symbol=${symbol}&period=${period}`;
            $.get(url, {}, (res) => {
                let a = [];
                a = res.data.data.reverse();
                a.forEach(function (item) {
                    item.timestamp = item.id * 1000;
                    item.volume = item.vol;
                });
                this.klineData = a;
                this.kLineChart.applyNewData(this.klineData);
                this.loading = false;

                if (callback) {
                    callback(a);
                }

                let ts = a[a.length - 1].id * 1000;
                switch (period) {
                    case "1min":
                        ts = moment(ts).format("m");
                        break;
                    case "5min":
                        ts = moment(ts).format("m");
                        break;
                    case "15min":
                        ts = moment(ts).format("m");
                        break;
                    case "30min":
                        ts = moment(ts).format("m");
                        break;
                    case "60min":
                        ts = moment(ts).format("m");
                        break;
                    case "4hour":
                        ts = moment(ts).format("H");
                        break;
                    case "1day":
                        ts = moment(ts).format("D");
                        break;
                }
                this.radio.temp = `${period}-${ts}`;
            });
        },
        percent(a) {
            let _a = [];
            let max_sum = 0;
            for (let i = 0; i < a.length; i++) {
                max_sum = Math.max(max_sum, Number(a[i].sum));
            }
            for (let i = 0; i < a.length; i++) {
                let item = a[i];
                item.percent = Math.round((item.sum / max_sum) * 100);
                _a.push(item);
            }
            return _a;
        },
        getQueryVariable(variable) {
            // var query = window.location.search.substring(1);
            var query = window.location.hash.substring(1);
            query = query.substring(query.lastIndexOf("?") + 1);
            var vars = query.split("&");
            for (var i = 0; i < vars.length; i++) {
                var pair = vars[i].split("=");
                if (pair[0] == variable) {
                    return pair[1];
                }
            }
            return false;
        },
        initWS() {
            this.ws = new WebSocket(`${this.g.wsUrl}/market.btcusdt.ticker`);
            this.ws.onopen = () => {
                console.log("websocket init ...");
                this.ws.send(JSON.stringify({ test: "测试数据" }));
            };
            this.ws.onclose = () => {
                console.log("websocket close ...");
                setTimeout(() => {
                    this.initWS();
                }, 1000);
            };
            this.ws.onmessage = (event) => {
                // {
                // "ch":"market.btcusdt.ticker",
                // "ts":1645709500954,
                // "tick":{
                //     "open":38978.8,"high":39006.55,"low":34322.26,"close":35457.08,"amount":19127.241598039418,"vol":688999460.942416,"count":794417,"bid":35462.49,"bidSize":0.40368,"ask":35462.5,"askSize":0.0002,"lastPrice":35462.5,"lastSize":0.018719
                //     }
                // }
                // amount: 4276.91316253405
                // close: 36950.23
                // count: 197122
                // high: 38779.82
                // id: 1645632000
                // low: 36631.32
                // open: 38516.9
                // vol: 161223694.4703626
                // item.timestamp = item.id * 1000;
                // item.volume = item.vol;
                // let lastData = chartDataList[chartDataList.length - 1];
                // lastData.close = res.price;
                // console.log(res, lastData);
                // let b = 100000000;
                // showLatePrice(Math.round(res.price * b) / b);
                // chart.updateData(lastData);
                let res = JSON.parse(event.data);
                if (res.ch == "market.btcusdt.ticker" && res.tick.close) {
                    let lastData = this.klineData[this.klineData.length - 1];

                    console.log(
                        this.radio.value,
                        res.ts,
                        moment(res.ts).format("YYYY-MM-DD HH:mm:ss"),
                        moment(lastData.id * 1000).format(
                            "YYYY-MM-DD HH:mm:ss"
                        ),
                        this.radio.temp
                    );

                    this.tick = res.tick;
                    this.tick.percent =
                        Math.round(
                            ((this.tick.close - this.tick.open) /
                                this.tick.close) *
                                10000
                        ) / 100;
                    lastData.id = Math.floor(res.ts / 1000);
                    lastData.close = this.tick.close;

                    // amount: 5022.188136008618
                    // close: 39411.86
                    // count: 267642
                    // high: 40300
                    // id: 1645804800
                    // low: 38535.16
                    // open: 39413.62
                    // vol: 197131984.51338908

                    this.kLineChart.updateData(lastData);
                }
            };
        },
        initList() {
            const _t = this;
            const url = this.g.apiUrl + "/api/caches?key=coincap-coin-list";
            $.get(url, (res) => {
                let a = res.data.value;
                a.forEach(function (item) {
                    item.priceUsd = _t.g.round(item.priceUsd, 1, 4);
                    item.changePercent24Hr = _t.g.round(
                        item.changePercent24Hr,
                        1,
                        2
                    );
                });
                this.list = a;
            });
        },
        init() {
            this.symbol = this.getQueryVariable("symbol");

            this.kLineChart = init("custom-theme-k-line");
            this.kLineChart.setStyleOptions(getThemeOptions("dark"));
            this.kLineChart.createTechnicalIndicator("VOL", true);
            this.kLineChart.setStyleOptions(getLanguageOptions(""));
            this.generatedKLineDataList(this.symbol, this.radio.value);

            this.initList();
            this.initWS();

            window.addEventListener("resize", () => {
                this.kLineChart.resize();
            });

            const RowCount = 7;
            let a = [];
            for (let i = 0; i < RowCount; i++) {
                let item = {
                    price: this.g.round(1000 + Math.random() * 1000),
                    amount: this.g.round(Math.random(), 1),
                    sum: this.g.round(Math.random(), 1),
                    percent: 0,
                };
                a.push(item);
            }
            this.orderbook_sell_list = this.percent(a);

            a = [];
            for (let i = 0; i < RowCount; i++) {
                let item = {
                    price: this.g.round(1000 + Math.random() * 1000),
                    amount: this.g.round(Math.random(), 1),
                    sum: this.g.round(Math.random(), 1),
                    percent: 0,
                };
                a.push(item);
            }
            this.orderbook_buy_list = this.percent(a);

            a = [];
            for (let i = 0; i < 30; i++) {
                let item = {
                    time: moment().format("HH:mm:ss"),
                    price: this.g.round(Math.random(), 1),
                    amount: this.g.round(Math.random(), 1),
                    act: Math.floor(Math.random() * 10) > 5 ? "buy" : "sell",
                };
                a.push(item);
            }
            this.market_trades_list = a;

            // setInterval(() => {
            //     const item = {
            //         time: moment().format("HH:mm:ss"),
            //         price: this.g.round(Math.random(), 1),
            //         amount: this.g.round(Math.random(), 1),
            //         act: Math.floor(Math.random() * 10) > 5 ? "buy" : "sell",
            //     };
            //     this.market_trades_list.pop();
            //     this.market_trades_list.splice(0, 0, item);
            // }, 1000);
        },
    },
    mounted() {
        this.init();
    },
};

const textColorDark = "#929AA5";
const gridColorDark = "#202020";
const axisLineColorDark = "#333333";
const crossTextBackgroundColorDark = "#373a40";

function getThemeOptions(theme) {
    const textColor = textColorDark;
    const gridColor = gridColorDark;
    const axisLineColor = axisLineColorDark;
    const crossLineColor = axisLineColorDark;
    const crossTextBackgroundColor = crossTextBackgroundColorDark;
    return {
        grid: {
            horizontal: {
                color: gridColor,
            },
            vertical: {
                color: gridColor,
            },
        },
        candle: {
            priceMark: {
                high: {
                    color: textColor,
                },
                low: {
                    color: textColor,
                },
            },
            tooltip: {
                text: {
                    color: textColor,
                },
            },
        },
        technicalIndicator: {
            tooltip: {
                text: {
                    color: textColor,
                },
            },
        },
        xAxis: {
            axisLine: {
                color: axisLineColor,
            },
            tickLine: {
                color: axisLineColor,
            },
            tickText: {
                color: textColor,
            },
        },
        yAxis: {
            axisLine: {
                color: axisLineColor,
            },
            tickLine: {
                color: axisLineColor,
            },
            tickText: {
                color: textColor,
            },
        },
        separator: {
            color: axisLineColor,
        },
        crosshair: {
            horizontal: {
                line: {
                    color: crossLineColor,
                },
                text: {
                    backgroundColor: crossTextBackgroundColor,
                },
            },
            vertical: {
                line: {
                    color: crossLineColor,
                },
                text: {
                    backgroundColor: crossTextBackgroundColor,
                },
            },
        },
    };
}

function getLanguageOptions(language) {
    return {
        candle: {
            tooltip: {
                labels:
                    language === "zh-CN"
                        ? ["时间：", "开：", "收：", "高：", "低：", "成交量："]
                        : language === "zh-HK"
                        ? ["時間：", "開：", "收：", "高：", "低：", "成交量："]
                        : [
                              "Time: ",
                              "Open: ",
                              "Close: ",
                              "High: ",
                              "Low: ",
                              "Volume: ",
                          ],
            },
        },
    };
}
</script>



















